class Hint extends Component {
  constructor(props) {
    super(props);
    this.state = {
      digitColor: "gray",
      digitValue: props.digitValue
    }
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    if(this.state.digitColor === "gray") {
      this.setState({digitColor: "green"});
    } else if(this.state.digitColor === "green") {
      this.setState({digitColor: "yellow"});
    } else if(this.state.digitColor === "yellow") {
      this.setState({digitColor: "red"});
    } else {
      this.setState({digitColor: "gray"});
    }
  }

  render() {
    return(
      <Button colorScheme={this.state.digitColor} onClick={this.handleClick}>{this.state.digitValue}</Button>
    );
  }
}

class Padlock extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lockStatus: props.lockStatus
    }
  }

  componentWillReceiveProps(nextProps) {
    this.setState({ lockStatus: nextProps.lockStatus });  
  }

  render() {
    if (this.state.lockStatus === "locked") {
      return(
        <LockIcon boxSize={60} />
      );  
    } else {
      return(
        <UnlockIcon boxSize={60} />
      );
  
    }
  }
}
